import React from 'react'

function Item(props) {
    return (
        
        <div className="col4">
            <img src={props.pimg} alt="pizza"/>
            <h4> {props.heading } </h4>
            <div class="rating">
                <i class="fa fa-star"></i>
                <i class="fa fa-star"></i>
                <i class="fa fa-star"></i>
                <i class="fa fa-star"></i>
                <i class="fa fa-star-o"></i>
            </div>
            <p>{props.price}</p>
        </div>
    )
}

export default Item;
